/* Add your CSS styles here */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
}

h1 {
  font-size: 28px;
  margin-bottom: 10px;
}

h2 {
  font-size: 20px;
  margin-bottom: 10px;
  color: #fc2626;
}

h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

h4 {
  font-size: 16px;
  margin-bottom: 5px;
}

section {
  margin-bottom: 20px;
}

ul {
  list-style-type: disc;
  margin-left: 20px;
}

p {
  margin-bottom: 10px;
}

/* Additional styles for specific elements */

section:nth-child(even) {
  background-color: #df19d2;
  padding: 10px;
}

section:nth-child(odd) {
  background-color: #297cb8;
  padding: 10px;
}

ul li {
  margin-bottom: 5px;
}

.cool-display {
  width: 400px;
  height: 300px;
  background-color: #f5f5f5;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.cool-display h1 {
  font-size: 30px;
  color: #333;
  margin-bottom: 10px;
}

.cool-display p {
  font-size: 18px;
  color: #555;
  margin-bottom: 20px;
}

.cool-display button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.cool-display button:hover {
  background-color: #0056b3;
}

.cool-display::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.6);
  transform: scale(1.5);
  opacity: 0;
  transition: transform 1s, opacity 0.5s;
}

.cool-display:hover::before {
  transform: scale(1);
  opacity: 1;
}

.cool-display::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
  animation: gradient-slide 3s linear infinite;
}

@keyframes gradient-slide {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

